import React from "react";
import { Fade } from "react-reveal";
import styled from "styled-components";
import { color } from "../Styles/GlobalStyle";
import ContactForm from "./ContactForm";

const Card = styled.div`
    text-align: center;
    background-color: ${color.peach};
    color: #fff;
    background-image: url(${require("../assets/contact/mobile/bg-pattern-hero-contact-mobile.svg").default});
    padding: 4.5rem 1.5rem;
    margin-bottom: 7.5rem;

    @media all and (min-width: 768px) {
        background-image: url(${require("../assets/contact/desktop/bg-pattern-hero-desktop.svg").default});
        text-align: left;
        border-radius: 10px;
        padding: 4.4375rem 3.625rem;
    }

    @media all and (min-width: 1200px) {
        margin: 0 auto 10rem;
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        column-gap: 8.5%;
        padding: 3.4375rem 8.6%;
    }

    @media all and (min-width: 1800px) {
        padding-inline: 4.5%;
    }
`;

const Heading = styled.h2`
    font-size: 2rem;
    line-height: 2.25rem;
    margin-bottom: 1.5rem;
    font-weight: 500;

    @media all and (min-width: 768px) {
        font-size: 3rem;
        line-height: 3rem;
        margin-bottom: 2rem;
    }
`;

const Description = styled.p`
    margin-bottom: 3rem;

    @media all and (min-width: 768px) {
        margin-bottom: 2.5rem;
        line-height: 1.8;
        letter-spacing: 0.5px;
    }

`;

export default function ContactUs() {
    return (
        <Fade top>
            <Card>
                <div>
                    <Heading>Contact Us</Heading>
                    <Description>
                        Ready to take it to the next level? Let’s talk about
                        your project or idea and find out how we can help your
                        business grow. If you are looking for unique digital
                        experiences that’s relatable to your users, drop us a
                        line.
                    </Description>
                </div>
                <ContactForm />
            </Card>
        </Fade>
    );
}
